import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { Title } from '@angular/platform-browser';

import { ChangeSetItem, ChangeSetUpdate,  } from 'ngrx-data';

import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, finalize, first, map, tap } from 'rxjs/operators';

import { tabRouteAnim } from '@animations';

import { areDifferent, sanitizeLegalName, SubSink } from '@core/utils';
import { Address, Company, companyKind, CompanyType, CompanyTypeCodes, PayrollFrequency, PayrollFrequencyCodes } from '@model';
import { AddressFormComponent } from '@shared/address/address-form.component';
import { AddressService, CompanyService } from '@entity';
import { AddressModalComponent } from '@shared';
import { changeItemFactory as cif } from '@utils';
import { environment } from '@environment';
import { TaxCollectorService } from '@core/services/tax-services';
import { WellKnownRoutes } from '@core';

@Component({
  selector: 'sp-company-info-general',
  templateUrl: 'company-info-general.component.html',
  styles: []
})

export class CompanyInfoGeneralComponent implements OnInit, OnDestroy {

  @ViewChild(AddressModalComponent)
  addressModal: AddressModalComponent;

  @ViewChild(AddressFormComponent)
  mainAddressForm: AddressFormComponent;

  /** Snapshot of the current company when component created */
  company: Company;
  companyTypeCodes =
    // All types except HH; you can only change to HH on About You page
    CompanyTypeCodes.codes.filter(code => code.value !== CompanyType.Household);

  /** Snapshot of the company's main address when component created */
  main: Address;
  originalAddressCodes: { geoCode: string, schoolDistrictCode?: number, psdCode: number, isVerified: boolean };

  private currentMain$: Observable<Address>;
  otherAddresses$: Observable<Address[]>;

  private isModalOpen = false;
  payrollFrequencyCodes = PayrollFrequencyCodes.codes;
  readOnly = false;
  showRemoved = false;

  private sub = new SubSink();

  constructor(
    private addressService: AddressService,
    private companyService: CompanyService,
    private ngTitle: Title,
    private taxCollectorService: TaxCollectorService,
  ) {  }

  ngOnInit(): void {
    window.scrollTo(0, 0);

    this.otherAddresses$ = this.addressService.entities$.pipe(
      map(addrs =>
        addrs
          .filter(a => !a.isHome && !a.isMain && (this.showRemoved || !a.isRemoved))
          .map(addr => Address.create(addr))
          .sort((l, r) => l.toString().localeCompare(r.toString()))
      )
    );

    this.companyService.currentCompany$.pipe(first()).subscribe(company => {
      this.company = {
        ...company
      };
      this.ngTitle.setTitle(`SP ${companyKind(company)} Info`);
    });

    this.currentMain$ = this.addressService.entities$.pipe(
      first(),
      map(addrs => addrs.find(addr => addr.isMain))
    );

    this.currentMain$.subscribe(
      main => {
        this.main = main ? { ...main } : Address.createNewCompanyAddress(this.company, true);
        this.originalAddressCodes = {
          geoCode: this.main.geoCode,
          schoolDistrictCode: this.main.schoolDistrictCode,
          psdCode: this.main.psdCode,
          isVerified: this.main.isVerified && !!this.main.geoCode
        };
      }
    );

    this.sub.sink = this.companyService.readOnly$.subscribe(ro => this.readOnly = ro);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

  fixLegalName() {
    if (this.company && this.company.legalName) {
      this.company.legalName = sanitizeLegalName(this.company.legalName);
    }
  }

  addAddress() {
    this.addressModal.addOrEditAddress({ isHome: false } as Address).subscribe();
  }

  get canVerifyMain() {
    const address = this.main;
    return !this.isModalOpen  && !this.readOnly && !address.isVerified &&
      address.street1 && address.city && address.state && address.zip;
  }

  editAddress(existingAddress: Address) {
    if (!this.readOnly) {
      this.addressModal.addOrEditAddress(existingAddress).subscribe();
    }
  }

  editMainAddress() {
    if (!this.isModalOpen && !this.readOnly) {
      this.isModalOpen = true;
      this.addressModal.addOrEditAddress(this.main)
        .pipe(finalize(() => this.isModalOpen = false))
        .subscribe(addr => {
          if (addr) {
            this.mainAddressForm.updateAddress(addr);
          }
        });
    }
  }

  removeAddress(existingAddress: Address) {
    this.addressModal.removeAddress(existingAddress).subscribe();
  }

  canLeaveAfterSave() {
    return this.save();
  }

  private save() {
    const saveUrl = environment.apiRoot + '/save/company';
    return combineLatest(this.getCompanyChange(), this.getMainAddressChange()).pipe(
      first(),
      concatMap(([companyChange, addressChange]) => {
        const changes: ChangeSetItem[] = [];
        if (companyChange) {
          changes.push(companyChange);
        }
        if (addressChange) {
          changes.push(addressChange);
        }
        if (changes.length) {
          return this.companyService.saveEntities({ changes }, saveUrl).pipe(
            concatMap(r => addressChange
              // If address changed, computeTaxes and wait until it finishes.
              // Note: taxes would be recomputed and saved anyway but
              // we want to wait until that tax save completes before leaving page
              // Totally hacky and saves taxes twice
              ? this.taxCollectorService.computeTaxes().pipe(map(t => true))
              : of(true)
            )
          );
        } else {
          return of(true);
        }
      })
    );
  }

  /** Update the current company with vm company if they differ */
  private getCompanyChange(): Observable<ChangeSetUpdate> {
    const company = this.company;

    return this.companyService.currentCompany$.pipe(
      first(),
      map(cachedCompany => {
        // Merge cached company with cleaned-up properties that were presented.
        const chgCompany = {
          ...cachedCompany,
          companyType: company.companyType,
          legalName: (company.legalName || '').trim(),
          name: (company.name || '').trim()
        };

        // Change to legal name unverifies the FEIN
        this.companyService.detectLegalNameChangeAfterVerify(chgCompany);
        return areDifferent(chgCompany, cachedCompany, 'Company') ? cif.update('Company', chgCompany) : null;
      })
    );
  }

  private getMainAddressChange(): Observable<ChangeSetItem> {
    const currentMain$ = this.addressService.entities$.pipe(
      first(),
      map(addrs => addrs.find(addr => addr.isMain))
    );

    return currentMain$.pipe(
      map(cachedMain => {
        if (cachedMain) {
          return areDifferent(this.main, cachedMain, 'Main Address') ? cif.update('Address', this.main) : null;
        } else {
          this.companyService.setIds(this.main);
          return cif.add('Address', this.main);
        }
      })
    );
  }

}
