import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { NgbTabset } from '@ng-bootstrap/ng-bootstrap';

import { concatMap, finalize, first, map, tap } from 'rxjs/operators';

import { AddressService, CompanyService } from '@entity';
import { areDifferent, sanitizeLegalName, SubSink } from '@core/utils';
import { Company } from '@model';
import { WellKnownRoutes } from '@core';

@Component({
  selector: 'sp-co-info',
  templateUrl: 'company-info.component.html'
})
export class CompanyInfoComponent implements OnInit, OnDestroy {

  @ViewChild('tabset')
  tabset: NgbTabset;

  backLink: string = WellKnownRoutes.AboutYou;
  nextLink: string;
  initialId: string; // initial tab id; tabsetSelect changes thereafter
  private sub = new SubSink();
  private tabLink: string; // link to the tab currently (or about to be) displayed

  tabTitles = [
    { id: 'general', title: 'General', link: 'general' },
    { id: 'schedule', title: 'Schedule', link: 'schedule' }
  ];


  company: Company;
  currentTabTitle: string;

  constructor(
    private companyService: CompanyService,
    private route: ActivatedRoute,
    private ngTitle: Title) {  }

  ngOnInit(): void {
    window.scrollTo(0, 0);
    this.nextLink = this.tabTitles[1].link;
    this.watchRouteChange();

    this.companyService.currentCompany$.pipe(first()).subscribe(company => {
      this.company = {
        ...company
      };
    });

    this.ngTitle.setTitle(`SP Company Info`);
  }

  ngOnDestroy(): void {
    this.sub.unsubscribe();
  }

     get disabled() {
      return false;
    }

    get disableNext() {
      return false;
    }


  /** Listen for route changes. Many of them re-use this component **/
  private watchRouteChange() {

    if (this.route.url !== undefined) {
      this.sub.sink = this.route.url.subscribe(url => {
        window.scrollTo(0, 0);
        this.tabLink = this.route.snapshot.firstChild.url.toString() || 'general';
        this.initialId = this.initialId || this.tabLink; // only useful first time
        // Wait a tick for tabs to draw
        setTimeout(() => this.updateCurrentTabInfo(this.tabLink));
      });
    }
  }

  private watchVmChange() {
    this.sub.sink = this.companyService.currentCompany$.subscribe(company => {
      this.company = company;
      this.setBrowserTabTitle();
    });
  }

  private setBrowserTabTitle() {
    this.ngTitle.setTitle(`SP Company ${this.currentTabTitle}`.trim());
  }

  /**
   * When the current tab changes, setup the tab-related info
   * such as the tab title, the browser tab title, the "next" link,
   */
  private updateCurrentTabInfo(tabLink: string) {
    if (this.tabset) {
      this.tabset.select(tabLink);
    }
    let tabIx = this.tabTitles.findIndex(tt => tt.link === tabLink);
    tabIx = tabIx < 0 ? 0 : tabIx;
    this.currentTabTitle = this.tabTitles[tabIx].title || '';
    const backTab = this.tabTitles[tabIx - 1];
    const nextTab = this.tabTitles[(tabIx += 1)];
    // On overflow, navigates away to the EmployeeList!
    this.backLink = backTab ? backTab.link : WellKnownRoutes.AboutYou;
    this.nextLink = nextTab ? nextTab.link : WellKnownRoutes.Employees;

    this.setBrowserTabTitle();
  }


}
