import { Injectable, NgModule } from '@angular/core';
import {  Routes, RouterModule } from '@angular/router';

import { Observable } from 'rxjs';

import { PageLeaveGuard } from '@core/routing-guards';
import { CompanyInfoComponent } from './company-info.component';
import { CompanyInfoGeneralComponent } from './company-info-general/company-info-general.component';
import { CompanyInfoScheduleComponent } from './company-info-schedule/company-info-schedule.component';

const routes: Routes = [
  {
    path: '',
    component: CompanyInfoComponent,
    canDeactivate: [PageLeaveGuard],
    children: [
      {
        path: '',
        component: CompanyInfoGeneralComponent,
        canDeactivate: [PageLeaveGuard],
      },
      {
        path: 'schedule',
        component: CompanyInfoScheduleComponent,
        canDeactivate: [PageLeaveGuard],
      },
      { path: '**', pathMatch: 'prefix', redirectTo: '' } // bad routes redirect to general
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
  providers: [ PageLeaveGuard ]
})
export class CompanyInfoRoutingModule {}
