import { Component, OnInit } from '@angular/core';
import { trigger, state, style, animate, transition, keyframes } from '@angular/animations';
import { ChangeSetUpdate, ChangeSetItem } from 'ngrx-data';

import { Observable } from 'rxjs';
import { first, map } from 'rxjs/operators';

import { Company, PayrollFrequency } from '@model';
import { CompanyService } from '@entity';
import { changeItemFactory as cif } from '@utils';
import { environment } from '@environment';
import { HolidaysService } from '@core/services/holidays.service';
import { ScheduleDates } from '@shared/date-range-pay-period/schedule-dates';
import { WellKnownRoutes, areDifferent } from '@core';

@Component({
  selector: 'sp-company-info-schedule',
  templateUrl: 'company-info-schedule.component.html',
  animations: [
    trigger('scaleEffect', [
      state(
        'small',
        style({
          opacity: 1
        })
      ),
      state(
        'large',
        style({
          opacity: 1
        })
      ),
      transition(
        'small <=> large',
        animate(
          '900ms ease-out',
          keyframes([
            style({ transform: 'scale(1)' }),
            style({ transform: 'scale(1.3)' }),
            style({ transform: 'scale(1)' })
          ])
        )
      )
    ])
  ]
})
export class CompanyInfoScheduleComponent implements OnInit {
  company: Company;

  checkMinDate = new Date(Date.now());
  defaultDate = new Date(Date.now());
  checkMaxDate = new Date(Date.now() + 360 * 24 * 3600 * 1000);

  iconFolder = '/assets/images/payroll-frequency/';
  companyDates: ScheduleDates;
  holidayDates: Date[];

  // Selected payroll frequency
  selectedFrequency: string;

  // Hovered payroll frequency option
  hoveredItem: string;

  tabTitles = [
    { id: 'general', title: 'General', link: '' },
    { id: 'schedule', title: 'Schedule', link: 'schedule' }
  ];

  // Payroll frequency items
  items = [
    { title: 'Weekly', caption: 'Weekly', state: 'small' },
    { title: 'Bi-Weekly', caption: 'Every Two Weeks', state: 'small' },
    { title: 'Semi-Monthly', caption: 'Twice a Month', state: 'small' },
    { title: 'Monthly', caption: 'Monthly', state: 'small' },
    { title: 'Quarterly', caption: 'Quarterly', state: 'small' }
  ];

  backLink: string = this.tabTitles[0].link;
  nextLink = WellKnownRoutes.Employees;

  constructor(private companyService: CompanyService, private holidayService: HolidaysService) {}

  ngOnInit() {
    this.holidayService.getHolidays().subscribe(holidays => (this.holidayDates = holidays));

    this.companyService.currentCompany$.pipe(first()).subscribe(company => {
      this.company = { ...company };
    });

    this.companyDates = {
      periodStartDate: this.company.periodStartDate,
      periodEndDate: this.company.periodEndDate,
      checkDate: this.company.firstCheckDate,
      secondPeriodStartDate: this.company.secondPeriodStartDate,
      secondPeriodEndDate: this.company.secondPeriodEndDate,
      secondCheckDate: this.company.secondCheckDate,
      firstPeriodEndOfMonth: this.company.firstPeriodEndOfMonth,
      firstCheckEndOfMonth: this.company.firstCheckEndOfMonth,
      secondPeriodEndOfMonth: this.company.secondPeriodEndOfMonth,
      secondCheckEndOfMonth: this.company.secondCheckEndOfMonth
    };
    this.selectedFrequency = this.company.payrollFrequency;
  }

  get showSelector() {
    return this.company != null && this.company.payrollFrequency != null;
  }

  setScheduleDates(scheduleDates: ScheduleDates) {
    this.company.firstCheckDate = scheduleDates.checkDate;
    this.company.periodStartDate = scheduleDates.periodStartDate;
    this.company.periodEndDate = scheduleDates.periodEndDate;
    (this.company.secondPeriodStartDate = scheduleDates.secondPeriodStartDate),
      (this.company.secondPeriodEndDate = scheduleDates.secondPeriodEndDate),
      (this.company.secondCheckDate = scheduleDates.secondCheckDate),
      (this.company.firstPeriodEndOfMonth = scheduleDates.firstPeriodEndOfMonth);
    this.company.firstCheckEndOfMonth = scheduleDates.firstCheckEndOfMonth;
    this.company.secondPeriodEndOfMonth = scheduleDates.secondPeriodEndOfMonth;
    this.company.secondCheckEndOfMonth = scheduleDates.secondCheckEndOfMonth;
  }

  selectFrequencyOption(selectedFrequency: string) {
    this.selectedFrequency = selectedFrequency;
    const preFrequencySelected = selectedFrequency.replace('-', '');
    this.company.payrollFrequency = (<any>PayrollFrequency)[preFrequencySelected];
    this.company.periodStartDate = null;
    this.company.periodEndDate = null;
    this.company.firstCheckDate = null;
    this.company.secondPeriodStartDate = null;
    this.company.secondPeriodEndDate = null;
    this.company.secondCheckDate = null;
    this.company.firstPeriodEndOfMonth = null;
    this.company.firstCheckEndOfMonth = null;
    this.company.secondPeriodEndOfMonth = null;
    this.company.secondCheckEndOfMonth = null;

    this.companyDates = {
      periodStartDate: this.company.periodStartDate,
      periodEndDate: this.company.periodEndDate,
      checkDate: this.company.firstCheckDate,
      secondPeriodStartDate: this.company.secondPeriodStartDate,
      secondPeriodEndDate: this.company.secondPeriodEndDate,
      secondCheckDate: this.company.secondCheckDate,
      firstPeriodEndOfMonth: this.company.firstPeriodEndOfMonth,
      firstCheckEndOfMonth: this.company.firstCheckEndOfMonth,
      secondPeriodEndOfMonth: this.company.secondPeriodEndOfMonth,
      secondCheckEndOfMonth: this.company.secondCheckEndOfMonth
    };
  }

  animateScale(index: any) {
    this.items[index].state = this.items[index].state === 'small' ? 'large' : 'small';
  }

  hoveredFrequencyOption(item: string) {
    this.hoveredItem = item;
  }

  isSelectedOrHovered(item: string) {
    return this.selectedFrequency === item || this.hoveredItem === item;
  }

  canLeaveAfterSave() {
    return this.save();
  }

  private save() {
    const saveUrl = environment.apiRoot + '/save/company';
    return this.getCompanyChange().pipe(
      first(),
      map(companyChange => {
        const changes: ChangeSetItem[] = [];
        if (companyChange) {
          changes.push(companyChange);
        }

        if (changes.length) {
          this.companyService.saveEntities({ changes }, saveUrl);
        }
        return true;
      })
    );
  }

  /** Update the current company with vm company if they differ */
  private getCompanyChange(): Observable<ChangeSetUpdate> {
    const company = this.company;

    return this.companyService.currentCompany$.pipe(
      first(),
      map(cachedCompany => {
        // Merge cached company with cleaned-up properties that were presented.
        const chgCompany = {
          ...cachedCompany,
          payrollFrequency: company.payrollFrequency,
          firstCheckDate: company.firstCheckDate,
          periodStartDate: company.periodStartDate,
          periodEndDate: company.periodEndDate,
          secondPeriodStartDate: company.secondPeriodStartDate,
          secondPeriodEndDate: company.secondPeriodEndDate,
          secondCheckDate: company.secondCheckDate,
          firstPeriodEndOfMonth: company.firstPeriodEndOfMonth,
          firstCheckEndOfMonth: company.firstCheckEndOfMonth,
          secondPeriodEndOfMonth: company.secondPeriodEndOfMonth,
          secondCheckEndOfMonth: company.secondCheckEndOfMonth
        };
        return areDifferent(chgCompany, cachedCompany, 'Company')
          ? cif.update('Company', chgCompany)
          : null;
      })
    );
  }
}
